.side-drawer{
    height:100%;
    background: white;
    box-shadow: 1px 0 6x rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left : 0;
    width: 70%;
    max-width: 400px;
    justify-content: center;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open{
    transform: translateX(0);

}

.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
}



.side-drawer li{
    margin: 0.5rem 0;
}

.side-drawer a{
    color: rgb(207, 192, 192);
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active{
    color: #4286f4;
}

@media(min-width: 796px){
    .side-drawer {
        display: none;
    }
}